    <transition name="fade-in">
      <router-view class="v-fill h-fill" />
    </transition>

    <div id="modal" :is="modal" v-bind="modalProps"></div>
    <transition name="fade-in">
      <div v-if="dialog" class="dialog cover fixed flex-column flex-center align-center">
        <div class="bg-base p3 rounded shadow">
          <h2 class="m2-bottom">{{dialog.message}}</h2>
          <div class="text-right">
            <button v-for="(title, i) in dialog.options" :key="i" class="button bg-primary base m1-right" @click="resolveDialog(title)">{{title}}</button>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import container from '@/js/container'

export default {
  data () {
    return {
      modal: null,
      modalProps: null,
      dialog: null
    }
  },
  provide: container,
  methods: {
    onScroll (event) {
    },
    resolveDialog (option) {
      this.dialog.resolve(option)
      this.dialog = null
    }
  }
}
</script>

